import React from "react";
import { Button, PageHeader, Space } from "antd";
import { LinkOutlined, ShoppingCartOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";
import Table from "components/ui/Table";
import { CustomColumns, Module } from "types";
import { useQuery } from "react-query3";
import DeleteButton from "components/ui/DeleteButton";

export function Modules() {
  const { data: components, isLoading } = useQuery<Module[]>(
    "/dashboardcomponent"
  );

  const columns: CustomColumns<Module>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
      editable: false,
    },
    {
      key: "version",
      title: "Version",
      dataIndex: "version",
      width: "min-content",
      align: "center",
      editable: false,
    },
    {
      key: "description",
      title: "Description",
      dataIndex: "description",
      width: "min-content",
      align: "center",
      editable: false,
    },
    {
      key: "path",
      title: "Path",
      dataIndex: "path",
      width: "min-content",
      editable: false,
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      render: (text, record) => {
        return (
          <Space>
            <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
          </Space>
        );
      },
    },
  ];

  return (
    <PageHeader title="Dashboard Components" subTitle="Additional component libraries available for dashboards." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/userinterfaces/dashboards/components" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Modules"
        actions={[
          <Link to="/admin/dashboards/marketplace">
            <Button type="primary" icon={<ShoppingCartOutlined />}>
              Go To Marketplace
            </Button>
          </Link>,
        ]}
        columns={columns}
        loading={isLoading}
        data={components}
      />
    </PageHeader>

  );
}

export default Modules;
